
import Confirm from 'vux/src/components/confirm/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { mapState } from "vuex";
import { MessageBox, Toast } from "mint-ui";
import vHeader from "@/components/v-header";
import wx from "@/assets/images/wx@2x.png";
import xy from "@/assets/images/xypay.png";
const qs = require("qs");
export default {
data() {
return {
xy: xy,
wx: wx,
show5: false,
pwd:'',
walletInfo:'',
payName: '微信支付',
selectVal:'wxpay',
price: this.getUrlParam("payPrice") || "",
orderNo: this.getUrlParam("orderNumbers") || ""
};
},
components: {
vHeader,
Confirm,
Group
},
computed: {
...mapState(["token"])
},
beforeCreate() {
document.title = "确认支付";
},
created() {
this.getWalletInfo();
},
methods: {
onConfirm5 (value) {
console.log('input value: ' + value);
this.$axios
.post(this.api.confirmPwd,JSON.stringify({'password': value }),{
headers: {
"content-type": "application/json",
"Authorization": this.token
}
})
.then(res => {
const resData = res.data;
if (resData.code !== 1) {
this.showTip('密码错误');
return;
}
this.payOrder();
})
.catch(res => {
this.noWallet = true;
this.showTip("获取我的钱包数据失败");
});

},
confirmPwd(){
this.$refs.confirm5.setInputValue('');
this.show5=true;
},
select(obj){
if(obj=='wxpay'){
this.selectVal='wxpay';
this.payName='微信支付';
}else{
this.selectVal='xypay';
this.payName='消费券支付';
}
},

getWalletInfo() {
this.$axios
.get(this.api.userWallet,
{
headers: { "Authorization": this.token }
})
.then(res => {
const resData = res.data;
if (resData.code !== 1) {
this.noWallet = true;
this.showTip(resData.message);
return;
}
this.walletInfo = resData.content;
console.log(this.walletInfo)
})
.catch(res => {
this.noWallet = true;
this.showTip("获取我的钱包数据失败");
});
},

getAllPrice(arrItem) {
let price = 0;
if (!arrItem || arrItem.length === 0) {
return price;
}
arrItem.forEach(val => {
price += val.product.salePrice * val.productCount;
});
return price;
},

cancelOrder() {
MessageBox({
title: "取消提示",
message: "您确定要取消此订单吗？",
showCancelButton: true
}).then(action => {
if (action === "confirm") {
this.$axios
.get(this.api.cancelOrder+this.orderNo, {
headers: { "Authorization": this.token }
})
.then(res => {
const resData = res.data;
if (resData.code !== 1) {
this.showTip("取消失败，请稍后重试");
return;
}
this.showTip("取消订单成功");
setTimeout(() => {
this.$router.go(-1);
}, 2000);
})
.catch(res => {
this.showTip("取消失败，请稍后重试");
});
}
});
},

payOrder() {

const loading = Toast({
message: "支付中...",
iconClass: "loading",
duration: 30000
});
this.$axios
.post(
this.api.otoPay,
JSON.stringify({
'channel': 2,
'orderNumber': this.orderNo,
'payType': this.selectVal
}),
{
headers: {
"content-type": "application/json",
"Authorization": this.token
}
}
)
.then(res => {
loading.close();
const resData = res.data;
if (resData.code !== 1) {
this.showTip(resData.msg);
setTimeout(()=>{
this.$router.push({path: "/mine"});
},2000)
return;
}

if(this.selectVal=='wxpay'){
this.weiXinPay(resData.content);
}else{
this.showTip("支付成功");
setTimeout(()=>{
this.$router.push({path: "/mine"});
},2000)
}
})
.catch(res => {
loading.close();
this.showTip("支付失败，请稍后重试");
});
},

weiXinPay(weiXinArg) {
let vue = this;

function onBridgeReady() {
WeixinJSBridge.invoke("getBrandWCPayRequest", JSON.parse(weiXinArg), res => {
if (res.err_msg == "get_brand_wcpay_request:ok") {

vue.showTip("支付成功", 2000, "ok");
setTimeout(() => {
vue.$router.replace({
path: "/goods/orders",
query: { status: 1 }
});
}, 2000);
} else if (res.err_msg == "get_brand_wcpay_request:cancel") {

vue.showTip("您已取消支付");
} else {

vue.showTip(res);
}
});
}

if (typeof WeixinJSBridge == "undefined") {
if (document.addEventListener) {
document.addEventListener(
"WeixinJSBridgeReady",
onBridgeReady,
false
);
} else if (document.attachEvent) {
document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
}
} else {
onBridgeReady();
}
}
}
};



